import React, {PureComponent} from 'react'
import {Form, Button, Row, Col, DatePicker, Checkbox, Input, message} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect, CsxSearch, CsxMultipleSearch} from 'Form'
import {
  API_USER_WAREHOUSELIST,
  API_PRODUCTSTOCKLOG_OPERATEIONTYPEKEYVALUE,
  API_PRODUCT_GETPRODUCTKEYVALUES,
  API_STOCKRELOCATION_RESERVOIRKEYVALUES,
  API_STOCKRELOCATION_GETSTORAGELOCATIONKEYVALUES
} from 'api'
const {RangePicker} = DatePicker
const FormItem = Form.Item
import moment from 'moment'
const formItemLayout = {
  labelCol: {
    xs: {span: 24},
    sm: {span: 8}
  },
  wrapperCol: {
    xs: {span: 24},
    sm: {span: 16}
  }
}

@connect(
  (state) => ({...state.stockLog, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  componentWillMount() {
    this.iniiDateOpt()
  }
  // 设置默认的时间  前一周 - 当天
  iniiDateOpt(values) {
    const {update} = this.props
    const preWeek = moment().subtract(7, 'd')
    update({searchParams: {initDate: [preWeek, moment()]}})
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size, userLoation} = this.props
    let reqObj = {...searchParams}
    const {initDate = []} = searchParams
    // 判断创建时时间是否为空
    if (!initDate || (Array.isArray(initDate) && initDate.length < 2)) {
      message.error('请选择创建时间后搜索')
    }
    handleGetList({searchParams: {...reqObj, page: 1, size}, userLoation})
  };
  // 重置
  reset = () => {
    const {update, userLoation} = this.props
    update({
      searchParams: {
        warehouseCode: {
          value: userLoation.value,
          children: userLoation.children
        }
      }
    })
    // 重置时间
    this.iniiDateOpt()
  };
  render() {
    const {
      searchParams: {
        orderCode,
        taskType,
        productCode,
        warehouseCode,
        productName,
        fromReservoirAreaCode,
        fromStoreLocationCode,
        toReservoirAreaCode,
        toStoreLocationCode,
        isCASError = false,
        initDate
      },
      userLoation,
      form: {
        getFieldDecorator
      }
    } = this.props

    return (
      <Form className={style.form}>
        <Row type="flex" justify="start">
          <Col span={6}>
            <div className="commonInput">
              <label>订单查询：</label>
              <div className="box">
                <Input
                  value={orderCode}
                  onChange={(e) => {
                    this.onInputChange({orderCode: e.target.value})
                  }}
                />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_PRODUCTSTOCKLOG_OPERATEIONTYPEKEYVALUE}
              label="操作类型"
              selectItem={{data: taskType, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({taskType: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxMultipleSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label="商品编码"
              searchParamsKey="productCode"
              searchParams={{
                warehouseCode: warehouseCode
                  ? warehouseCode.value
                  : userLoation.value
              }}
              selectItem={{
                data: productCode,
                key: 'key',
                valueKey: ['key', 'value']
              }}
              onChangeSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label="商品名称"
              searchParamsKey="productName"
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{
                data: productName,
                key: 'key',
                valueKey: ['key', 'value']
              }}
              onChangeSelect={(value, n) => {
                this.onInputChange({productName: value})
              }}
              onSelect={(value) => {
                this.onInputChange({productName: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_STOCKRELOCATION_RESERVOIRKEYVALUES}
              label="从库区"
              selectItem={{
                data: fromReservoirAreaCode,
                key: 'key',
                valueKey: ['key', 'value']
              }}
              onChangeSelect={(value) => {
                this.onInputChange({fromReservoirAreaCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_STOCKRELOCATION_GETSTORAGELOCATIONKEYVALUES}
              label="从储位"
              searchParams={{
                reservoirAreaCode:
                  fromReservoirAreaCode && fromReservoirAreaCode.value
              }}
              errMessage="请先选择从库区"
              selectItem={{
                data: fromStoreLocationCode,
                key: 'key',
                valueKey: ['key', 'value']
              }}
              onChangeSelect={(value) => {
                this.onInputChange({fromStoreLocationCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_STOCKRELOCATION_RESERVOIRKEYVALUES}
              label="至库区"
              selectItem={{
                data: toReservoirAreaCode,
                key: 'key',
                valueKey: ['key', 'value']
              }}
              onChangeSelect={(value) => {
                this.onInputChange({toReservoirAreaCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_STOCKRELOCATION_GETSTORAGELOCATIONKEYVALUES}
              label="至储位"
              searchParams={{
                reservoirAreaCode:
                  toReservoirAreaCode && toReservoirAreaCode.value
              }}
              errMessage="请先选择至库区"
              selectItem={{
                data: toStoreLocationCode,
                key: 'key',
                valueKey: ['key', 'value']
              }}
              onChangeSelect={(value) => {
                this.onInputChange({toStoreLocationCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_USER_WAREHOUSELIST}
              label="仓库"
              selectItem={{
                data: warehouseCode
                  ? warehouseCode
                  : {
                    value: userLoation.value,
                    children: userLoation.children
                  },
                key: 'warehouseCode',
                valueKey: ['warehouseCode', 'warehouseName']
              }}
              onChangeSelect={(value) => {
                this.onInputChange({warehouseCode: value})
              }}
            />
          </Col>
          <Col span={7}>
            <div className="commonInput">
            <label className="require">创建时间：</label>
              <div className="box" style={{marginRight: 0}}>
                <RangePicker
                  onChange={(moment, string) => {
                    this.onInputChange({initDate: moment})
                  }}
                  format={'YYYY-MM-DD'}
                  value={initDate}
                  allowClear={false}
                />
              </div>
            </div>
          </Col>
          <Col span={6} pull={0}>
            <Checkbox
              checked={isCASError ? true : false}
              onChange={(e) => {
                this.onInputChange({isCASError: e.target.checked})
              }}
            >
              异常订单
            </Checkbox>
          </Col>
          <Col span={5} pull={1}>
            <Row type="flex" justify="end">
              <Button
                type="primary"
                className={style.mr10}
                onClick={this.query}
              >
                查询
              </Button>
              <Button
                type="primary"
                className={style.mr10}
                onClick={this.reset}
              >
                重置
              </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



