import React, { PureComponent } from 'react'
import {
  Table,
  Modal,
  message
} from 'antd'
const confirm = Modal.confirm
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'

@connect(
  state => ({ ...state.stockLog, userLoation: state.userLoation }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '任务ID',
      width: 150,
      dataIndex: 'orderCode',
      key: 'orderCode'
    }, {
      title: '来源订单',
      width: 150,
      dataIndex: 'inOutCode',
      key: 'inOutCode'
    }, {
      title: '操作类型',
      width: 100,
      dataIndex: 'taskTypeDesc',
      key: 'taskTypeDesc'
    }, {
      title: '商品编码',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品名称',
      width: 200,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '库存调整量',
      width: 100,
      dataIndex: 'adjustmentQty',
      key: 'adjustmentQty'
    }, {
      title: '单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '从库区',
      width: 100,
      dataIndex: 'fromReservoirAreaCode',
      key: 'fromReservoirAreaCode'
    }, {
      title: '从储位',
      width: 100,
      dataIndex: 'fromStoreLocationCode',
      key: 'fromStoreLocationCode'
    }, {
      title: '从储位库存调整前数量',
      width: 100,
      dataIndex: 'fromAdjustmentBeforeQty',
      key: 'fromAdjustmentBeforeQty'
    }, {
      title: '从储位库存调整后数量',
      width: 100,
      dataIndex: 'fromAdjustmentAfterQty',
      key: 'fromAdjustmentAfterQty'
    }, {
      title: '至库区',
      width: 150,
      dataIndex: 'toReservoirAreaCode',
      key: 'toReservoirAreaCode'
    }, {
      title: '至储位',
      width: 100,
      dataIndex: 'toStoreLocationCode',
      key: 'toStoreLocationCode'
    }, {
      title: '至储位库存调整前数量',
      width: 100,
      dataIndex: 'toAdjustmentBeforeQty',
      key: 'toAdjustmentBeforeQty'
    }, {
      title: '至储位库存调整后数量',
      width: 100,
      dataIndex: 'toAdjustmentAfterQty',
      key: 'toAdjustmentAfterQty'
    }, {
      title: '创建时间',
      width: 150,
      dataIndex: 'createTime',
      key: 'createTime'
    }, {
      title: '完成时间',
      width: 150,
      dataIndex: 'finishTime',
      key: 'finishTime'
    }, {
      title: '操作人',
      width: 100,
      dataIndex: 'createBy',
      key: 'createBy'
    }, {
      title: '备注',
      width: 150,
      dataIndex: 'remark',
      key: 'remark'
    }, {
      title: '成本凭证',
      width: 250,
      dataIndex: 'costCertificateCode',
      key: 'costCertificateCode'
    }]
  }
  componentDidMount() {
    // const {handleGetList, searchParams, page, size, userLoation} = this.props
    // if (!userLoation || !userLoation.value ) return
    // handleGetList({searchParams: {page, size, ...searchParams}, userLoation})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, searchParams, page, size, userLoation } = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({ searchParams: { page: 1, size: newProps.size, ...newProps.searchParams }, userLoation: newProps.userLoation })
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const { handleGetList, searchParams, page, size, userLoation } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({ searchParams: { page, size, ...searchParams, isAsc: true, columns: sorter.field }, userLoation })
      } else {
        handleGetList({ searchParams: { page, size, ...searchParams, isAsc: false, columns: sorter.field }, userLoation })
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({ searchParams: { page: pagination.current, size: pagination.pageSize, ...searchParams }, userLoation })
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{ x: 'max-content', y: 400 }}
          onChange={this.onChange}
          pagination={pagination}
        />
      </div>
    )
  }
}



