import * as actionType from './actionType'
import {getData} from 'util'
import {API_PRODUCTSTOCKLOG_LOADLIST_PAGE, API_PRODUCTSTOCKLOG_EXPORT, API_GET_MENUS_PERMISSIONS_BANS} from 'api'
import moment from 'moment'

//同步
export const update = (object) => ({
  type: actionType.STOCKLOG_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.STOCKLOG_UPDATE, data: {tableLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      i === 'productName' ? reqObj[i] = reqObj[i].children : reqObj[i] = reqObj[i].value
      // reqObj[i] = reqObj[i].value
    }
  }
  if (reqObj.initDate && reqObj.initDate.length) {
    reqObj.beginDate = moment(reqObj.initDate[0]).format('YYYY-MM-DD')
    reqObj.endDate = moment(reqObj.initDate[1]).format('YYYY-MM-DD')
  }
  delete reqObj.initDate
  if (!reqObj.warehouseCode) {
    reqObj.warehouseCode = reqData.userLoation && reqData.userLoation.value
  }
  getData({api: API_PRODUCTSTOCKLOG_LOADLIST_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.STOCKLOG_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.STOCKLOG_FILE
    })
  })
}

// 导出
export const handleActionExport = (reqData, callBack) => (dispatch) => {
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  if (reqObj.initDate && reqObj.initDate.length) {
    reqObj.beginDate = moment(reqObj.initDate[0]).format('YYYY-MM-DD')
    reqObj.endDate = moment(reqObj.initDate[1]).format('YYYY-MM-DD')
  }
  delete reqObj.initDate
  getData({api: API_PRODUCTSTOCKLOG_EXPORT, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.STOCKLOG_FILE
    })
  })
}

// 获取页面按钮权限
export const GetButtonPower = (reqData) => (dispatch) => {
  getData({api: API_GET_MENUS_PERMISSIONS_BANS, ...reqData}).then(({data}) => {
    console.log(data)
    // 相应的逻辑判断
    let Power = {}
    data.map((o) => {
      Power[o.menuCode] = true
    })
    dispatch({
      type: actionType.STOCKLOG_UPDATE,
      data: {
        ...Power
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.STOCKLOG_FILE
    })
  })
}



