import React, {PureComponent} from 'react'
import {
  message,
  Button
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {getNowPageId} from '../../global/components/menuData'

@connect(
  state => ({...state.stockLog, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  componentDidMount() {
    const {GetButtonPower} = this.props
    const MEUN = JSON.parse(localStorage.getItem('b2bUserMenu'))
    const ID = getNowPageId(MEUN, window.location.pathname)
    GetButtonPower({urlParams: [ID]})
  }
  // 导出
  handleExport = () => {
    const {handleActionExport, searchParams, page, size, userLoation} = this.props
    handleActionExport({searchParams: {page, size, warehouseCode: userLoation.value, ...searchParams, productName: searchParams.productName ? searchParams.productName.children : ''}}, () => {
      message.success('导出成功')
    })
  }

  render() {
    const {stockLogExport} = this.props
    return (
      <div className={style['table-tool']}>
        {
          stockLogExport && <Button type="primary" className={style.btn} onClick={() => {
            this.handleExport()
          }}>导出</Button>
        }
      </div>
    )
  }
}



